import React, { Component, Fragment } from 'react';
import '../../styles/navbar.css';
import {items} from './Variables';
import LoginModal from './LoginModal';
import YourCart from './YourCart';

class ListItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'nav-item',
        }
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
    }

    handleLogin() {
        document.getElementById('login').style.visibility = 'visible';
        document.getElementsByClassName('login-modal')[0].classList.add('jo');
    }
    
    handleMouseEnter() {
        this.setState({
            class: this.state.class + ' is-focused'
        })
    }

    handleMouseLeave() {
        this.setState({
            class: 'nav-item'
        })
    }

    render() {
        const result = (child) => {
            if (child !== undefined) {
                return (
                    <div className="nav-dropdown">
                        <div className="nav-dropdown-container">
                            {
                                child.map(item => (
                        
                                    <div className="nav-dropdown-column">
                                        {
                                            item.map(chunk => (
                                                <a className={chunk.class} href={chunk.href}>{chunk.text}</a>
                                            ))
                                        }
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                );
            }
            return <Fragment></Fragment>
        }
        return (
            <li onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.class}>
                {
                    (this.props.href) ? <a href={this.props.href}>{this.props.text}</a> : <button type={this.props.type} onClick={this.handleLogin}>{this.props.text}</button>
                }
                { result(this.props.child) }
            </li>
        );
    }
}


class SearchBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'search',
            clicked: false
        }
        this.handleOnBlur = this.handleOnBlur.bind(this);
        this.handleOnFocus = this.handleOnFocus.bind(this);
    }

    handleOnFocus() {
        this.setState({
            class: this.state.class + ' is-focused',
            clicked: true
        })
    }

    handleOnBlur() {
        this.setState({
            class: 'search',
            clicked: false
        })    
    }

    render() {
        return (
            <div className={this.state.class} id="search">
                    <input onFocus={this.handleOnFocus} onBlur={this.handleOnBlur} placeholder="SEARCH"/>
                    <img src="assets/icons/search.png" alt="search-icon"/>
            </div>
        );
    }
}

class Navbar extends Component {
    render() {
        return (
            <header>
                <nav className="navbar">
                <img src="assets/icons/logo.png" id="logo" alt="logo"/>
                <div className="brand">
                    <h1>ABE SHOES</h1>
                    <p>Đồng hành cùng bạn</p>
                </div>
                <ul>
                    { items.map(item => <ListItem {...item} />) }
                </ul>
                <YourCart />
                <SearchBox />
            </nav>
            <LoginModal />
            </header>
        );
    }
};

export default Navbar;