import React, { Fragment } from 'react';
import '../styles/main.css';
import Navbar from './Navbar/Navbar';
import Carousel from './Carousel/Carousel';
import Blog from './Blog/Blog';
import Features from './Features/Features';
import Products from './Products/Products';
import Footer from './Footer/Footer';
import FlashSale from './FlashSale/FlashSale';
import '../styles/animation.css';

export default function Main() {
    var appear = [
        {
            className: 'flash-sale-container',
            added: false,
            index: 0,
            animation: 'jo',
        }, {
            className: 'products-container',
            added: false,
            index: 0,
            animation: 'ltr50',
        }, {
            className: 'products-container',
            added: false,
            index: 1,
            animation: 'rtl50',
        },  {
            className: 'products-container',
            added: false,
            index: 2,
            animation: 'ltr50',
        }, {
            className: 'mail-register',
            added: false,
            index: 0,
            animation: 'rtl50',
        }, {
            className: 'footer-info',
            added: false,
            index: 0,
            animation: 'utd',
        },
    ];
    return (
        <Fragment>
            <Navbar />
            <div className="content">
                <div className="board">
                    <Carousel />
                    <Blog />
                </div>
                <Features />
                <FlashSale />
                <Products />
            </div>
            <Footer />
            {
            window.onload = () => {
                function myFunction() {
                    const {pageYOffset, innerHeight} = window, screenHeight = window.screen.availHeight;
                    // console.log('%c innerHeight, screenHeight, zoom, pageYOffset', 'font-weight: bold; color: red; background-color: black;');
                    // console.log(innerHeight, screenHeight, zoom, pageYOffset);
                    let zoom =  innerHeight / screenHeight;
                    appear.forEach(elem => {
                        if (!elem.added) {
                            const node = document.getElementsByClassName(elem.className)[elem.index];
                            elem.thresh = pageYOffset + node.getBoundingClientRect().top;
                            // console.log('%c pageYOffset, innerHeight, elem.thresh', 'color: red;');
                            // console.log(pageYOffset, innerHeight, elem.thresh);
                            if (pageYOffset + innerHeight >= elem.thresh + 0.25 / zoom * innerHeight) {
                                node.classList.add(elem.animation);
                                elem.added = true;
                            }
                        }
                    })
                }
                myFunction();
                window.onresize = () => {
                    myFunction();
                };
                window.addEventListener("scroll", () => myFunction());
            }
        }
        </Fragment>
    );
} 