import React, { Component } from 'react';
import '../../styles/yourcart.css';

export default class YourCart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'your-cart'
        }
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }

    handleMouseEnter() {
        let ct = document.getElementsByClassName('cart-tooltip-container')[0];
        ct.classList.add('show');
    }

    handleMouseLeave() {
        let ct = document.getElementsByClassName('cart-tooltip-container')[0];
        ct.classList.remove('show');
    }

    render() {
        return (
            <div className="your-cart" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>
                <img src="assets/icons/cart.png" alt="cart-icon"/>
                <div>GIỎ HÀNG</div>
                <span id="cart-number">2</span>

                <div role="tooltip" className="cart-tooltip-container">
                    <div className="cart-tooltip">
                        <h2>SẢN PHẨM MỚI THÊM</h2>
                        <div className="product-thumbnail">
                            <img className="thumbnail" src="assets/images/1.png" />
                            <div className="details">
                                <div class="header">
                                    <span class="name">SPECIAL 001</span>
                                    <span className="price">
                                        1,190,000đ
                                        <span class="quantities"> x{1}</span>
                                    </span>
                                </div>
                                <div class="footer">
                                    <span class="categorize">Phân loại size: 42</span>
                                    <button class="remove">Xóa</button>
                                </div>
                            </div>
                        </div>
                        <div className="product-thumbnail">
                            <img className="thumbnail" src="assets/images/2.png" />
                            <div className="details">
                                <div class="header">
                                    <span class="name">SPECIAL 002</span>
                                    <span className="price">
                                        1,190,000đ
                                        <span class="quantities"> x{1}</span>
                                    </span>
                                </div>
                                <div class="footer">
                                    <span class="categorize">Phân loại size: 39</span>
                                    <button class="remove">Xóa</button>
                                </div>
                            </div>
                        </div>
                    </div>                            
                </div>
            </div>
        );
    }
}
